import { Component, OnInit } from '@angular/core';
import { ModalControllerService } from 'src/app/components/modal/services/modal-controller.service';
import { AttendedEventComponent } from './components/attended-event/attended-event.component';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {

  public title: string = 'About';

  constructor(private modal: ModalControllerService) { }

  ngOnInit() {
  }

  openAttendedEvents() {
    this.modal.create({
      id: 'about-events-attended',
      component: AttendedEventComponent,
      header: 'Events Attended',
      animate: true
  })
    
  this.modal.open('about-events-attended');
  }

}
